/** @format */

// utility functions for getting info out of the data

import * as df from "./dates";

// convenience function to return find data for a given date
export function getDayFindData(date, counts) {
  let result = {
    numToFind: getNumToFind(date, counts),
    totalFinds: getTotalFinds(date, counts),
    normalFinds: getNormalFinds(date, counts),
    labFinds: getLabFinds(date, counts),
    goal: getGoal(counts),
  };

  return result;
}

// returns the find count data and summary for the given number of days
export function getFindCountData(days, counts) {
  const data = df.getNDays(days, counts);

  const summaryInfo = {
    generated: new Date(counts.creationTime).toLocaleString(),
    goal: counts.goal,
    next7days: df.getReqFindsNextWeek(data.dates, counts.goal),
  };

  return {
    data: data,
    summary: summaryInfo,
  };
}

// returns the value from dateIndex corresponding to the given date
function getDateIndex(date, counts) {
  if (date in counts.dateIndex) {
    return counts.dateIndex[date];
  }

  return "";
}

// returns the total number of finds for the given date
export function getTotalFinds(date, counts) {
  const index = getDateIndex(date, counts);

  if (index === "") {
    return -1;
  }

  return counts.dates[index].findCount;
}

// returns the number of lab finds for the given date (if any)
export function getLabFinds(date, counts) {
  const index = getDateIndex(date, counts);

  if (index === "") {
    return -1;
  }

  return counts.dates[index].labs ? counts.dates[index].labs : 0;
}

// returns the number of "normal" finds (everything except labs) for the
// give date
export function getNormalFinds(date, counts) {
  return getTotalFinds(date, counts) - getLabFinds(date, counts);
}

// returns the daily goal
export function getGoal(counts) {
  return counts.goal;
}

// returns the number of find required for the given date.
// if the current count exceeds the goal, zero is returned
export function getNumToFind(date, counts) {
  const f = getGoal(counts) - getTotalFinds(date, counts);
  return f < 0 ? "0" : f;
}
