/** @format */

// geocaching.com link helper functions

import * as geo from "./Geoloc";

// basic search URL
// https://www.geocaching.com/play/search?origin=N+41%c2%b0+1.366,+W+111%c2%b0+56.419&nfb[0]=llenseuqm&f=2&o=2&utr=false

// creates a URL for the geocaching.com search interface
export function getGCSearchURL(loc) {
  const baseURL = "https://www.geocaching.com/play/search";
  const userParam = "nfb[0]=llenseuqm";
  const foundByUser = "f=2";
  const ownedByUser = "o=2";
  const utr = "utr=false";

  // create the origin parameter by replacing spaces with +
  const origin = "origin=" + loc.replace(/\s/g, "+");

  // build the URL and return
  return `${baseURL}?${origin}&${userParam}&${foundByUser}&${ownedByUser}&${utr}`;
}

// browse map URL
// https://www.geocaching.com/map/#?ll=41.022767,-111.940317&z=14
// input: position object having latitude and longitude fields
export function getGCBrowseMapURL(pos) {
  const baseURL = "https://www.geocaching.com/map/#";
  const ll = `ll=${geo.convertToString(pos)}`;
  const zoom = "z=14";

  return `${baseURL}?${ll}&${zoom}`;
}
