// convenience methods for dealing with holidays

export const isHoliday = (date, year, holidays) => {
  try {
    if (holidays) {
      return buildFullDate(year, date) in holidays;
    }
  } catch (err) {
    console.log("Error in isHoliday():");
    console.log(err.message);
  }
  return false;
};

export const getHolidayName = (date, year, holidays) => {
  if (!isHoliday(date, year, holidays)) {
    return "";
  }

  return holidays[buildFullDate(year, date)];
};

// convenience method to concatenate the year and date into
// the format: YYYY-MM-DD
const buildFullDate = (year, date) => {
  return `${year}-${date}`;
};
