/** @format */

// implements a hook to set the theme and indicate when it is loaded
// at startup.
//
// Based on code from:
// https://css-tricks.com/a-dark-mode-toggle-with-react-and-themeprovider/

import { useEffect, useState } from "react";
import { Themes } from "./Themes";

export const useCustomTheme = (defaultTheme) => {
  const [theme, setInternalTheme] = useState(defaultTheme);
  const [componentMounted, setComponentMounted] = useState(false);

  const setCustomTheme = (customTheme) => {
    // store just the name of the theme
    window.localStorage.setItem("theme", customTheme.palette.name);
    setInternalTheme(customTheme);
  };

  // expects th is a theme
  const setTheme = (th) => {
    setCustomTheme(th);
  };

  useEffect(() => {
    // get the name of the theme
    const localThemeName = window.localStorage.getItem("theme");

    if (localThemeName) {
      setInternalTheme(Themes[localThemeName]);
    }
    setComponentMounted(true);
  }, []);

  return [theme, setTheme, componentMounted];
};
