/** @format */

// implements the to-do list for the next week

import React from "react";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import { makeStyles, withTheme } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import * as df from "./dates";

function NextWeek(props) {
  const { onClose, open, week } = props;
  const small = useMediaQuery(props.theme.breakpoints.down("sm"));

  const useStyles = makeStyles((theme) => ({
    root: {},
    content: {
      minWidth: "300px",
      padding: "10px",
      backgroundColor: theme.palette.background.default,
    },
    closeButton: {
      position: "absolute",
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.day.normBkgnd,
      padding: "5px",
    },
    spacer: {
      height: "10px",
    },
    gridRoot: {
      display: "flex",
      flexDirection: "column",
    },
    summary: {
      padding: "10px",
      color: theme.palette.day.number,
      backgroundColor: theme.palette.day.normBkgnd,
    },
    weekGrid: {
      display: "flex",
      flexDirection: "row",
      flexWrap: "nowrap",
      justifyContent: "space-between",
      alignItems: "center",
    },
    dayCell: {},
    dayText: {
      padding: "5px",
      color: theme.palette.day.number,
    },
    findText: {
      paddingBottom: "7px",
      textAlign: "center",
      color: theme.palette.find.someReq,
    },
    findZero: {
      color: "transparent",
    },
    dayPaper: {
      backgroundColor: theme.palette.day.normBkgnd,
    },
  }));

  const classes = useStyles();

  const handleClose = () => {
    onClose("");
  };

  const getSummaryInfo = () => {
    // loop over the days and generate the summary info
    let finds = 0;
    let days = 0;

    for (let i = 0; i < week.dates.length; i++) {
      const day = week.dates[i];

      if (day.findCount < week.goal) {
        days++;
        finds += week.goal - day.findCount;
      }
    }

    return (
      <Paper className={classes.summary}>
        <Typography>Daily Goal: {week.goal}</Typography>
        <Typography>Total Finds Needed This Week: {finds}</Typography>
        <Typography>Days Needing Finds: {days}</Typography>
      </Paper>
    );
  };

  /* Structure of week
      dates: Array(7)
      0: {date: "01-03", findCount: 2, dayname: "Sunday"}
      1: {date: "01-04", findCount: 7, dayname: "Monday"}
      2: {date: "01-05", findCount: 2, dayname: "Tuesday"}
      3: {date: "01-06", findCount: 8, dayname: "Wednesday"}
      4: {date: "01-07", findCount: 2, dayname: "Thursday"}
      5: {date: "01-08", findCount: 1, dayname: "Friday"}
      6: {date: "01-09", findCount: 2, dayname: "Saturday"}
      length: 7
      __proto__: Array(0)
      goal: 4
      number: 7
      startDate: "01-03"
      status: "ok"
  */

  const getNumToFind = (finds) => {
    if (finds < week.goal) {
      return week.goal - finds;
    } else {
      return 0;
    }
  };

  const getDayHeader = (day) => {
    const width = small ? 2 : 3;
    const dayName = day.dayname.slice(0, width);
    const dayNum = df.getDayOfMonth(day.date);

    return `${dayNum} ${dayName}`;
  };

  const getDayCell = (day) => {
    const numToFind = getNumToFind(day.findCount);

    let findStyles = `${classes.findText}`;
    if (numToFind === 0) {
      findStyles = `${classes.findText} ${classes.findZero}`;
    }

    return (
      <Grid item className={classes.dayCell} key={day.date}>
        <Paper className={classes.dayPaper}>
          <Typography
            className={classes.dayText}
            variant={"overline"}
            noWrap={true}>
            {getDayHeader(day)}
          </Typography>
          <Typography className={findStyles} variant="h5">
            {numToFind}
          </Typography>
        </Paper>
      </Grid>
    );
  };

  const getWeek = () => {
    return (
      <Grid container item className={classes.weekGrid} spacing={1} xs={12}>
        {week.dates.map((day) => getDayCell(day))}
      </Grid>
    );
  };

  return (
    <>
      <Dialog onClose={handleClose} open={open} className={classes.root}>
        <div className={classes.content}>
          <DialogTitle id="nextweek">
            Number To Find This Week
            <IconButton className={classes.closeButton} onClick={handleClose}>
              <CloseIcon />
            </IconButton>
          </DialogTitle>
          <Grid container className={classes.gridRoot}>
            {getWeek()}
            <div className={classes.spacer} />
            {getSummaryInfo()}
          </Grid>
        </div>
      </Dialog>
    </>
  );
}

export default withTheme(NextWeek);
