/** @format */

// various convenience functions for dealing with the server

// returns the api key used to contact the server
const getKey = () => {
  return "mEuqAh6oKH";
};

// returns the server address without / on the end
const getServerAddr = () => {
  return "https://young-stream-78694.herokuapp.com";
};

// returns a complete endpoint URL with the key attached as
// the first parameter:
// example: https://..../api/endpoint?key=....
//
const getURL = (endpoint) => {
  return `${getServerAddr()}/api/${endpoint}?key=${getKey()}`;
};

// returns the weather api address with the key attached as the first param
export const getWxURL = () => {
  return getURL("weather");
};

// returns the sunrise/sunset times api address with the key as the first param
export const getSunURL = () => {
  return getURL("sun");
};

// returns the holidays api address with the key as the first param
export const getHolidaysURL = () => {
  return getURL("hday/all");
};

// returns the find data api address with the key as the first param
export const getFindsURL = () => {
  return getURL("all");
};
