/** @format */

import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";

const useStyles = makeStyles((theme) => ({
  root: {
    borderRadius: "7px",
    backgroundColor: theme.palette.summary.background,
  },
  box: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  zeroReq: {
    color: theme.palette.find.noneReq,
  },
  moreThanZero: {
    color: theme.palette.find.someReq,
  },
  summaryFigs: {
    textAlign: "center",
  },
  text: {
    color: theme.palette.summary.text,
  },
}));

export default function SummaryCard(props) {
  const classes = useStyles();
  const { generated, goal, next7days } = props.info;
  return (
    <>
      <Card className={classes.root} variant="outlined">
        <CardContent>
          <Box className={classes.box}>
            <div>
              <Typography className={classes.text}>Updated:</Typography>
              <Typography className={classes.text}>Goal per day:</Typography>
              <Typography className={classes.text}>Next 7 days:</Typography>
            </div>
            <div className={classes.summaryFigs}>
              <Typography className={classes.text}>{generated}</Typography>
              <Typography className={classes.text}>{goal}</Typography>
              <Typography
                className={
                  next7days === 0 ? classes.zeroReq : classes.moreThanZero
                }>
                {next7days}
              </Typography>
            </div>
          </Box>
        </CardContent>
      </Card>
    </>
  );
}
