/** @format */

import { useEffect, useState } from "react";

// list of the number of days that can be used to fill the grid
export const DayChoices = [
  7,
  14, // default
  21,
  30,
];

// implements a hook to set the days to be displayed and indicate when it
// is loaded at startup.
//
// Based on code from:
// https://css-tricks.com/a-dark-mode-toggle-with-react-and-themeprovider/

export const useDays = (defaultDays) => {
  const [days, setInternalDays] = useState(defaultDays);

  const setDaysAndStore = (d) => {
    window.localStorage.setItem("days", d);
    setInternalDays(d);
  };

  const setDays = (d) => {
    setDaysAndStore(d);
  };

  useEffect(() => {
    const d = window.localStorage.getItem("days");

    if (d) {
      setInternalDays(d);
    }
  }, []);

  return [days, setDays];
};
