/** @format */

// implements the header of Grid7, consisting of 7 HeaderDay cells
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import HeaderDay from "./HeaderDay";

const useStyles = makeStyles((theme) => ({
  headerRoot: {
    display: "flex",
    flexWrap: "nowrap",
    justifyContent: "space-between",
    alignItems: "center",
  },
  cell: {
    borderStyle: "solid",
    borderWidth: "2px",
    borderColor: theme.palette.background.default,
    backgroundColor: theme.palette.primary.main,
    paddingTop: "10px",
    paddingBottom: "10px",
  },
}));

// expects a property named values that is an array containing
// seven day header values
const Header = (props) => {
  const classes = useStyles();
  const { values } = props;

  const getHeaderCell = (value) => {
    return (
      <Grid item className={classes.cell} key={value} xs={12}>
        <HeaderDay text={value} />
      </Grid>
    );
  };

  return (
    <>
      <Grid item container className={classes.headerRoot}>
        {values.map((value) => getHeaderCell(value))}
      </Grid>
    </>
  );
};

export default Header;
