/** @format */

// helper methods for dealing with geo-locations

// returns a nicely formatted geolocation in the format:
// (N|S) DD MM.mmm (E|W) DDD MM.mmm
export function getFormattedGeoLoc(pos) {
  return `${getFormattedLatitude(pos.latitude)}, ${getFormattedLongitude(
    pos.longitude
  )}`;
}

export function getFormattedLatitude(lat) {
  const dir = lat >= 0 ? "N" : "S";
  return `${dir} ${convertToDegMinutes(lat)}`;
}

export function getFormattedLongitude(lon) {
  const dir = lon >= 0 ? "E" : "W";
  return `${dir} ${convertToDegMinutes(lon)}`;
}

// converts decimal degrees to degrees and decimal minutes
// returns a string in the format: [D][D]D MM.mmm (any - sign is removed)
export function convertToDegMinutes(dd) {
  let deg = dd | 0; // truncate to get degrees without sign
  const frac = Math.abs(dd - deg); // get fractional part
  let min = frac * 60; // get the minutes

  // check to see if the minutes round up to more than a whole degree
  if (min >= 60.0) {
    deg++;
    min = min - 60.0;
  }

  return `${Math.abs(deg)}° ${min.toFixed(3)}`;
}

// converts a position object to a string suitable for use with the
// geocaching.com browse map in the format YY.YYYYY,XXX.XXXXXX where
// Y is the digits of latitude and X is the digits of longitude
export function convertToString(pos) {
  return `${pos.latitude.toFixed(6)},${pos.longitude.toFixed(6)}`;
}
