/** @format */

// implements a week of days (7)
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import DayCell from "./DayCell";
import { isHoliday, getHolidayName } from "./Holidays";

// expects a property named values that is an array containing
// the values for each of the seven days:
// values: [
//    { number: '1', text: '12', subtext: 'Finds', status:'empty|used' },
//    ...,
// ]
const Week = (props) => {
  const { values, dayOnClick, holidays } = props;

  const useStyles = makeStyles((theme) => ({
    weekRoot: {
      display: "flex",
      flexWrap: "nowrap",
      justifyContent: "space-between",
      alignItems: "center",
    },
    day: {
      borderColor: theme.palette.background.default,
    },
    today: {
      borderColor: theme.palette.today.border,
    },
    holiday: {
      borderColor: theme.palette.holiday.border,
    },
    unused: {
      borderColor: theme.palette.background.default,
    },
    commonBorder: {
      borderStyle: "solid",
      borderWidth: "3px",
    },
  }));

  const classes = useStyles();

  const getDayCell = (value) => {
    const { status, today, rawDate, index, year } = value;
    const cell = {
      day: rawDate,
      offset: index,
      today: today,
      year: year,
      holiday: false,
      holidayName: "",
    };

    let styleName = today ? classes.today : classes.day;

    if (!today && isHoliday(rawDate, year, holidays)) {
      styleName = classes.holiday;
      cell.holiday = true;
      cell.holidayName = getHolidayName(rawDate, year, holidays);
    }

    if (status === "unused") {
      styleName = classes.unused;
    }

    return (
      <Grid
        item
        className={`${classes.commonBorder} ${styleName}`}
        key={Math.random()}
        xs={12}
        onClick={() => {
          dayOnClick(cell);
        }}>
        <DayCell value={value} key={Math.random()} />
      </Grid>
    );
  };

  return (
    <>
      <Grid item container className={classes.weekRoot}>
        {values.map((value) => getDayCell(value))}
      </Grid>
    </>
  );
};

export default Week;
