/** @format */

// implements the details dialog
import React, { useState, useEffect } from "react";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Link from "@material-ui/core/Link";
import Paper from "@material-ui/core/Paper";
import WbSunnyIcon from "@material-ui/icons/WbSunny";
import Brightness2Icon from "@material-ui/icons/Brightness2";
import GpsFixedIcon from "@material-ui/icons/GpsFixed";
import axios from "axios";

import { getDayFindData } from "./GetData";
import { getFormattedGeoLoc } from "./Geoloc";
import { getGCBrowseMapURL, getGCSearchURL } from "./GCLinks";
import * as df from "./dates";
import * as st from "./SunTimes";
import { getWxURL, getSunURL } from "./Server";

function DayDetail(props) {
  const { onClose, selectedValue, open, cell, position, allData } = props;

  const [isLoaded, setIsLoaded] = useState(false);
  const [weather, setWeather] = useState({});
  const [sunTimesLoaded, setSunTimesLoaded] = useState(false);
  const [sunTimes, setSunTimes] = useState({
    results: {
      sunrise: "",
      sunset: "",
    },
    status: "fail",
  });

  const date = cell.day;

  const qDT = df.getFullDate(cell.offset);

  const dayFindData = getDayFindData(date, allData);

  // const location = "q=41.02,-111.94"; // Kaysville

  useEffect(() => {
    const location = `loc=${position.latitude},${position.longitude}`;
    const sunLoc = `lat=${position.latitude}&lon=${position.longitude}`;
    // getWxURL: https://..../api/weather?key=...
    const wxURL = `${getWxURL()}&${location}&dt=${qDT}`;

    axios.get(wxURL).then((response) => {
      setWeather(response.data);
      setIsLoaded(true);
    });

    // get the sunrise/sunset times
    const sunURL = `${getSunURL()}&${sunLoc}&date=${qDT}`;

    axios.get(sunURL).then((response) => {
      // console.log("sun times:");
      // console.log(response.data);
      setSunTimes(response.data);
      setSunTimesLoaded(true);
    });
  }, [qDT, position.latitude, position.longitude]);

  const useStyles = makeStyles((theme) => ({
    root: {},
    content: {
      minWidth: "300px",
      backgroundColor: theme.palette.day.normBkgnd,
      padding: "10px",
    },
    closeButton: {
      position: "absolute",
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.day.number,
      padding: "5px",
    },
    paper: {
      padding: "10px",
    },
    spacer: {
      height: "10px",
    },
    apilink: {
      display: "flex",
      justifyContent: "flex-end",
    },
    finds: {
      paddingLeft: "10px",
      color:
        dayFindData.numToFind > 0
          ? theme.palette.find.someReq
          : theme.palette.find.noneReq,
    },
    numtofind: {
      display: "flex",
    },
    gclinksdiv: {
      display: "flex",
      justifyContent: "flex-end",
    },
    gclink: {
      color: theme.palette.day.altBkgnd,
      paddingLeft: "20px",
    },
    weatherFact: {
      paddingLeft: "15px",
    },
    sun: {
      display: "flex",
      alignItems: "center",
    },
    suntime: {
      paddingLeft: "10px",
    },
    holiday: {
      color: theme.palette.holiday.border,
    },
  }));

  const classes = useStyles();

  const handleClose = () => {
    onClose(selectedValue);
  };

  const getCurrentWeather = () => {
    // if the selected day is not today, then the current weather is not displayed
    if (!cell.today) {
      return <></>;
    } else {
      const currentTemp = `${weather.current.temp_f.toFixed(
        0
      )}, feels like: ${weather.current.feelslike_f.toFixed(0)} °F`;
      const currentIcon = `https:${weather.current.condition.icon}`;
      const currentWind = `${weather.current.wind_mph.toFixed(
        0
      )}, gust: ${weather.current.gust_mph.toFixed(0)} MPH`;
      return (
        <>
          <div className={classes.spacer}></div>
          <Typography>Current weather in {weather.location.name}:</Typography>
          <Typography className={classes.weatherFact}>
            Temp: {currentTemp}
          </Typography>
          <Typography className={classes.weatherFact}>
            Wind: {currentWind}
          </Typography>
          <Typography className={classes.weatherFact}>
            {weather.current.condition.text}
          </Typography>
          <div className={classes.weatherFact}>
            <img src={currentIcon} alt="weather"></img>
          </div>
        </>
      );
    }
  };

  // returns true if the weather object contains a forecast
  const isForecastAvailable = () => {
    try {
      if (weather.forecast.forecastday.length > 0) {
        return true;
      }
    } catch {
      // ignore the error
    }

    return false;
  };

  const getForecastWeather = () => {
    // if there is no forecast available (API docs say 10 days in the future
    // but it seems to work for something like 16)
    if (!isForecastAvailable()) {
      return <></>;
    }

    const forecast = weather.forecast.forecastday[0];
    const temps = `High/Low: ${forecast.day.maxtemp_f.toFixed(
      0
    )}/${forecast.day.mintemp_f.toFixed(0)} °F`;

    // get the current hour and use it to get the forecast for the next hour
    // this will seem more representative of the coming weather
    let hour = df.getCurrentHour();

    if (hour < 23) {
      hour++;
    }

    const forecastHour = weather.forecast.forecastday[0].hour[hour];

    const condition = `${forecastHour.condition.text}`;
    const icon = `https:${forecastHour.condition.icon}`;
    const precipChance = `Chance Precip: ${forecast.day.daily_chance_of_rain}%`;
    const snowChance = `Chance Snow: ${forecast.day.daily_chance_of_snow}%`;
    const maxWind = `Max. Wind: ${forecast.day.maxwind_mph.toFixed(0)} MPH`;
    const uv = `UV Index: ${forecast.day.uv}`;

    return (
      <>
        <Typography>Forecast:</Typography>
        <Typography className={classes.weatherFact}>{temps}</Typography>
        <Typography className={classes.weatherFact}>{maxWind}</Typography>
        <Typography className={classes.weatherFact}>{precipChance}</Typography>
        <Typography className={classes.weatherFact}>{snowChance}</Typography>
        <Typography className={classes.weatherFact}>{uv}</Typography>
        <Typography className={classes.weatherFact}>{condition}</Typography>
        <div className={classes.weatherFact}>
          <img src={icon} alt="weather"></img>
        </div>
      </>
    );
  };

  const getSunTimes = () => {
    let sunrise = "";
    let sunset = "";

    // if the weather forecast is available, use the info from it, otherwise
    // use the free api
    if (isForecastAvailable()) {
      sunrise = weather.forecast.forecastday[0].astro.sunrise;
      sunset = weather.forecast.forecastday[0].astro.sunset;
    } else {
      if (sunTimesLoaded) {
        sunrise = st.getLocalSunriseTime(sunTimes);
        sunset = st.getLocalSunsetTime(sunTimes);
      } else {
        sunrise = "N/A";
        sunset = "N/A";
      }
    }
    return (
      <>
        <div className={classes.sun}>
          <WbSunnyIcon fontSize="small" />
          <Typography
            className={classes.suntime}>{`Sunrise: ${sunrise}`}</Typography>
        </div>
        <div className={classes.sun}>
          <Brightness2Icon fontSize="small" />
          <Typography
            className={classes.suntime}>{`Sunset: ${sunset}`}</Typography>
        </div>
      </>
    );
  };

  const getSunAPILink = () => {
    // configure the link depending on which API is used

    let linkHref = "";
    let linkText = "";

    if (isForecastAvailable()) {
      linkHref = "https://www.weatherapi.com/";
      linkText = "WeatherAPI.com";
    } else {
      linkHref = st.getAPIurl();
      linkText = "Sunrise/Sunset API";
    }

    return (
      <>
        <div className={classes.spacer}></div>
        <div className={classes.spacer}></div>
        <Link
          className={classes.apilink}
          href={linkHref}
          target="_blank"
          rel="noreferrer"
          color="textSecondary"
          variant="body2"
          underline="always">
          {linkText}
        </Link>
      </>
    );
  };

  const getWeatherInfo = () => {
    // if there is no weather info at all:
    if (!weather) {
      return (
        <>
          <Typography>Weather Not Available</Typography>
        </>
      );
    }

    return (
      <>
        {getCurrentWeather()}
        {getForecastWeather()}
      </>
    );
  };

  const getGeolocItems = () => {
    // if the position is not available, don't render anything
    // that needs it
    if (position.status === "ok") {
      const loc = getFormattedGeoLoc(position);
      return (
        <>
          <div className={classes.spacer}></div>
          <div className={classes.sun}>
            <GpsFixedIcon fontSize="small" />
            <Typography className={classes.suntime}>
              Location: {`${loc}`}
            </Typography>
          </div>
          <div className={classes.gclinksdiv}>
            <Link
              className={classes.gclink}
              href={`${getGCSearchURL(loc)}`}
              target="_blank"
              rel="nonreferrer"
              underline="always">
              Search
            </Link>
            <Link
              className={classes.gclink}
              href={getGCBrowseMapURL(position)}
              underline="always"
              target="_blank"
              rel="nonreferrer">
              GC Map
            </Link>
          </div>
          <div className={classes.spacer}></div>
          {getSunTimes()}
          <div className={classes.spacer}></div>
          {getWeatherInfo()}
          {getSunAPILink()}
        </>
      );
    } else {
      return (
        <>
          <Typography>Location: unavailable</Typography>
        </>
      );
    }
  };

  const getHoliday = () => {
    if (!cell.holiday) {
      return "";
    }

    return (
      <>
        <div className={classes.spacer}></div>
        <Typography
          className={classes.holiday}>{`${cell.holidayName}`}</Typography>
      </>
    );
  };

  if (!isLoaded) {
    return <div>Loading...</div>;
  } else {
    return (
      <>
        <Dialog onClose={handleClose} open={open} className={classes.root}>
          <div className={classes.content}>
            <DialogTitle id="daydetailtitle">
              {`${df.getLongDateText(date)}`}
              <IconButton className={classes.closeButton} onClick={handleClose}>
                <CloseIcon />
              </IconButton>
            </DialogTitle>
            <Paper elevation={3} className={classes.paper}>
              <Typography>
                Total finds: {`${dayFindData.totalFinds}`}
              </Typography>
              <Typography>
                Normal finds: {`${dayFindData.normalFinds}`}
              </Typography>
              <Typography>Lab finds: {`${dayFindData.labFinds}`}</Typography>
              <Typography>Goal: {`${dayFindData.goal}`}</Typography>
              <div className={classes.numtofind}>
                <Typography>Number to find: </Typography>
                <Typography
                  className={
                    classes.finds
                  }>{`${dayFindData.numToFind}`}</Typography>
              </div>
              {getHoliday()}
              {getGeolocItems()}
            </Paper>
          </div>
        </Dialog>
      </>
    );
  }
}

export default DayDetail;
