/** @format */

// functions for manipulating and formatting the sunrise, sunset times

export function getLocalSunriseTime(sun) {
  if (sun.status !== "OK") {
    return "UNKNOWN";
  }

  return getLocaleTime(sun.results.sunrise);
}

export function getLocalSunsetTime(sun) {
  if (sun.status !== "OK") {
    return "UNKNOWN";
  }

  return getLocaleTime(sun.results.sunset);
}

function getLocaleTime(t) {
  const d = new Date(t);
  return d.toLocaleTimeString();
}

export function getAPIurl() {
  return "https://sunrise-sunset.org/api";
}
