/** @format */

const { DateTime } = require("luxon");

//
// Various date support functions
//

// creates a date string in the format MM-DD
// does not error check the input
export function buildDate(month, day) {
  const date =
    month.toString().padStart(2, "0") + "-" + day.toString().padStart(2, "0");
  return date;
}

// returns a string containing the given date in the format MM-DD
export function getFormattedDate(date) {
  const month = date.getMonth() + 1;
  const day = date.getDate();
  return buildDate(month, day);
}

// returns todays date in the format MM-DD
export function getToday() {
  const today = new Date();
  return getFormattedDate(today);
}

// returns the current hour as an integer: 0-23
export function getCurrentHour() {
  const now = new Date();
  return now.getHours();
}

// Returns the given number of days, beginning with the given start date. If startDate
// or number is not valid, an empty array is returned.  The maximum number of dates
// returned is the complete array (366)
export function getDates(startDate, number, counts) {
  let result = {
    startDate: startDate,
    number: number,
    status: "fail",
    dates: [],
    goal: 0,
  };

  // parse the number, if not a number, return an error
  let num = parseInt(number, 10);
  if (isNaN(num)) {
    return result;
  }

  // if the requested number of days is more than one year,
  // set it to one year
  const len = counts.dates.length;
  if (num > len) {
    num = len;
    result.number = num;
  }

  const indices = getNDayIndices(startDate, result.number, counts);

  if (indices.length < 1) {
    return result;
  }

  const dayNames = getDayNames(startDate, result.number);

  for (let i = 0; i < num; i++) {
    const index = indices[i];
    const d = counts.dates[index];
    d.dayname = dayNames[i];
    result.dates.push(d);
  }

  result.goal = counts.goal;
  result.status = "ok";

  return result;
}

// Convenience function that returns an array of the indices of the
// given number of days, starting with today
// Inputs:
//   startDate: starting date in the format MM-dd
//   num: number of days
//   counts: the raw find data
// Returns:
//   array of date indices on success,
//   empty array on failure
export function getNDayIndices(startDate, num, counts) {
  let indices = [];

  const today = DateTime.local();
  let day = DateTime.local(
    today.year,
    getMonthFromDate(startDate),
    getDayOfMonth(startDate)
  );

  for (let i = 0; i < num; i++) {
    const d = day.toFormat("MM-dd");

    if (d in counts.dateIndex) {
      indices.push(counts.dateIndex[d]);
    } else {
      return [];
    }

    day = day.plus({ days: 1 });
  }

  return indices;
}

// Convenience function that returns the next 14 days
export function get14Days(counts) {
  return getNDays(14, counts);
}

// Convenience function that returns the next num days
export function getNDays(num, counts) {
  return getDates(getToday(), num, counts);
}

// Returns an array of strings containing the names of the
// next <number> days, beginning with the given start date
// in the current year.
// startDate: date in the form MM-DD
// number: integer number of days
export function getDayNames(startDate, number) {
  const names = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];

  let result = [];

  // get the starting day in this year
  const year = new Date().getFullYear().toString();
  const sd = new Date(startDate + "-" + year);
  const startDoW = sd.getDay();

  for (let i = 0; i < number; i++) {
    let index = (startDoW + i) % names.length;
    result.push(names[index]);
  }

  return result;
}

// returns the number of finds required in the next 7 days
// dates: array of dates with findcounts, if length is less than 7,
//        zero is returned.
// goal: the desired minimum number of finds in the next 7 days,
//       must be greater than zero
export function getReqFindsNextWeek(dates, goal) {
  let result = 0;
  let days = 0;

  if (dates.length < 7 || goal < 1) {
    return result;
  }

  for (let i = 0; i < 7; i++) {
    if (dates[i].findCount < goal) {
      result += goal - dates[i].findCount;
      days++;
    }
  }

  if (result !== 0) {
    return `${result} -- (${days} day${days > 1 ? "s" : ""})`;
  } else {
    return result;
  }
}

// Helper function that returns the day of the month as an integer.
//
// Input: string containing the date in the format MM-DD
//
// Output: integer day of the month for the given date
export function getDayOfMonth(date) {
  // 01234
  // MM-DD
  const dayString = date.substring(3);
  const parsed = parseInt(dayString, 10);

  if (isNaN(parsed)) {
    return 0;
  }

  return parsed;
}

// Helper function that returns the month as an integer.
//
// Input: string containing the date in the format MM-DD
//
// Output: month for the given date
export function getMonthFromDate(date) {
  // 01234
  // MM-DD
  const monthString = date.substring(0, 2);
  const parsed = parseInt(monthString, 10);

  if (isNaN(parsed)) {
    return 0;
  }

  return parsed;
}
// Helper function to return the month as a three-character string
//
// Input: string containing the date in the format MM-DD
//
// Output: 3-character string representing the month for the given date
export function getShortMonthName(date) {
  return getMonthName(date, true);
}

// Helper function to get the name of the month represented by the date. The
// returned name can be either a short 3-char string or the full name.
//
// Inputs: date, string in the format MM-DD
//         short: boolean, true indicates short name, otherwise full
//
// Output: string containing the string representation of the month
function getMonthName(date, short) {
  // 01234
  // MM-DD
  const month = parseInt(date.substring(0, 2), 10);
  const monthNames = [
    { short: "dummy", long: "dummy" },
    { short: "Jan", long: "January" },
    { short: "Feb", long: "February" },
    { short: "Mar", long: "March" },
    { short: "Apr", long: "April" },
    { short: "May", long: "May" },
    { short: "Jun", long: "June" },
    { short: "Jul", long: "July" },
    { short: "Aug", long: "August" },
    { short: "Sep", long: "September" },
    { short: "Oct", long: "October" },
    { short: "Nov", long: "November" },
    { short: "Dec", long: "December" },
  ];

  if (isNaN(month) || month < 1 || month > 12) {
    return "";
  }

  return short ? monthNames[month].short : monthNames[month].long;
}

export function getLongDateText(date) {
  if (date) {
    return `${getDayOfMonth(date)} ${getMonthName(date)}`;
  } else {
    return "";
  }
}

// returns an array of 7 days for the current week
// input: the count data
export function getCurrentWeek(counts) {
  // get the day of the week of today and use it
  // to get the first day of the week
  const today = DateTime.local();
  const dayOfWeek = today.weekday; // note: Sunday = 7, Monday = 1, Tue = 2
  const startOfWeek =
    dayOfWeek === 7 ? today : today.minus({ days: dayOfWeek });
  const date = startOfWeek.toFormat("MM-dd");

  return getDates(date, 7, counts);
}

// adds the offset to the current date and returns the result
// in the format: YYYY-MM-DD
//
// input: the number of days in the future, 0 = today, 1= tomorrow, etc.
export function getFullDate(offset) {
  const today = DateTime.local();
  const newDay = today.plus({ days: offset });
  return newDay.toFormat("yyyy-MM-dd");
}
