/** @format */

// implements the invidual days in a row
import React from "react";
import Typography from "@material-ui/core/Typography";
import { makeStyles, withTheme } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import CheckIcon from "@material-ui/icons/Check";

// expects the following properties:
//   number: the number of the date
//   text: the main text to be displayed
//   subtext: smaller text to be displayed
//   status: unused|ok|low
//   today: boolean indicating whether this date is today
//   monthFlag: boolean indicating whether this cell should be the
//              primary month color or the alternative month color
const DayCell = (props) => {
  const { number, text, subtext, status, today, monthFlag } = props.value;
  const theme = props.theme;

  let normalBkgnd = theme.palette.day.normBkgnd;
  let normalNumClr = theme.palette.day.number;
  let someReqNormClr = theme.palette.find.someReq;
  let noneReqNormClr = theme.palette.find.noneReq;

  if (monthFlag) {
    normalBkgnd = theme.palette.day.altBkgnd;
    normalNumClr = theme.palette.day.altNumber;

    if (theme.palette.find.altNoneReq) {
      noneReqNormClr = theme.palette.find.altNoneReq;
    }

    if (theme.palette.find.altSomeReq) {
      someReqNormClr = theme.palette.find.altSomeReq;
    }
  }

  const colors = {
    unused: {
      backgroundColor: theme.palette.background.default,
      numberColor: theme.palette.background.default,
      findColor: theme.palette.background.default,
    },
    ok: {
      backgroundColor: normalBkgnd,
      numberColor: normalNumClr,
      findColor: noneReqNormClr,
    },
    low: {
      backgroundColor: normalBkgnd,
      numberColor: normalNumClr,
      findColor: someReqNormClr,
    },
    today: {
      backgroundColor: theme.palette.today.background,
      numberColor: theme.palette.today.number,
    },
  };

  let backgroundColor = colors.today.backgroundColor;
  let numberColor = colors.today.numberColor;

  if (!today) {
    backgroundColor = colors[status].backgroundColor;
    numberColor = colors[status].numberColor;
  }

  const useStyles = makeStyles((theme) => ({
    cellRoot: {
      display: "flex",
      backgroundColor: backgroundColor,
      height: "100px",
      [theme.breakpoints.down("sm")]: { flexDirection: "column" },
      [theme.breakpoints.down("sm")]: { justifyContent: "space-between" },
      [theme.breakpoints.up("md")]: { flexDirection: "row" },
      cursor: "pointer",
    },
    cellNumber: {
      color: numberColor,
      paddingLeft: "4px",
    },
    cellText: {},
    cellSubText: {},
    finds: {
      color: colors[status].findColor,
      [theme.breakpoints.up("md")]: { paddingTop: "15px" },
      [theme.breakpoints.down("sm")]: { paddingLeft: "7px" },
      textAlign: "center",
      justifyContent: "center",
      alignContent: "center",
    },
    checkPad: {
      [theme.breakpoints.up("md")]: { paddingTop: "10px" },
    },
  }));

  const classes = useStyles();

  const getFindContent = () => {
    if (status === "ok") {
      return (
        <>
          <div className={classes.checkPad}></div>
          <CheckIcon fontSize="large" />
        </>
      );
    } else {
      return (
        <>
          <Typography variant="h4" className={classes.cellText}>
            {text}
          </Typography>
          <Typography variant="subtitle2" className={classes.cellSubText}>
            {subtext}
          </Typography>
        </>
      );
    }
  };

  return (
    <>
      <Grid item container className={classes.cellRoot}>
        <Grid item sm={12} md={4}>
          <Typography variant="subtitle1" className={classes.cellNumber}>
            {number}
          </Typography>
        </Grid>
        <Grid item className={classes.finds} sm={12} md={8}>
          {getFindContent()}
        </Grid>
      </Grid>
    </>
  );
};

export default withTheme(DayCell);
