/** @format */

import * as df from "./dates";

// contains the code used to generate the week structures used for Grid7

// Parses the dates in the data to an array of week objects
//
// Inputs: data { dates: [], goal: integer}
//
// Output: [week], where week is an array of 7 day objects
export function getWeeks(data) {
  const days = getDays(data);
  return convertDaysToWeeks(days);
}

// Parses the dates into an array of day objects for use by Grid7
// inputs:
//
// { dates: [], goal: integer }
//
// outputs:
//
//   days: [
//            {
//              number: integer (day of the month),
//              text: string to appear in large text (find count),
//              subtext: string to appear under the large text ("Finds"),
//              status: 'unused' | 'low' | 'ok',
//              today: true | undefined
//            },
//            ...
//         ]
//
// the number of days are padded front and back to be a multiple of 7
function getDays(data) {
  const { dates, goal } = data;

  let days = [];

  // get day of the week of the first day (today)
  const today = new Date();
  const currentDoW = today.getDay();
  const currentDoM = today.getDate();

  // pad the days array with unused days before the first day
  for (let i = 0; i < currentDoW; i++) {
    days.push(createUnusedDay(""));
  }

  // fill the array with the given days
  for (let j = 0; j < dates.length; j++) {
    days.push(createDayObject(dates[j], goal, currentDoM, j));
  }

  // pad the end of the array to the next multiple of seven
  let numToPad = 7 - (days.length % 7);

  // don't add blank weeks
  if (numToPad === 7) {
    numToPad = 0;
  }

  for (let k = 0; k < numToPad; k++) {
    days.push(createUnusedDay(""));
  }

  // setup the month toggle flags and years on every day
  let monthFlag = false;
  let year = today.getFullYear();

  for (let i = 0, len = days.length; i < len; i++) {
    // toggle the month flag, only if this is not the first date shown
    if (i > 0 && days[i].first) {
      monthFlag = !monthFlag;

      // if the date is 01-01, then increment the year
      if (days[i].rawDate === "01-01") {
        year++;
      }
    }

    days[i].monthFlag = monthFlag;
    days[i].year = year;

    // make sure only the first occurrence of "today" is highlighted
    days[i].today = days[i].today && !monthFlag;
  }

  return days;
}

// Creates a date object using the information in the given date and goal
//
// Inputs: date in the format specified in calendar.json
//         goal: integer goal of finds per day
//         currentDoM: integer current day of the month
//         index: the index of the day within the grid (0 = first day)
//
// Output: day object in the format:
//   number: day of the month (first day includes month)
//   text: findCount of the day
//   subtext: "Finds"
//   status: 'low' | 'ok' depending on the goal and findCount
//   today: true for the current day, false otherwise
//   first: true if the day is the 1st of the month
//   rawDate: the original date in format MM-DD
function createDayObject(date, goal, currentDoM, index) {
  const dom = df.getDayOfMonth(date.date);

  let result = {
    number: dom === 1 ? df.getShortMonthName(date.date) + " " + dom : dom,
    text: date.findCount,
    subtext: "Finds",
    status: date.findCount < goal ? "low" : "ok",
    today: dom === currentDoM,
    first: dom === 1,
    rawDate: date.date,
    index: index,
  };

  return result;
}

// returns an usused (aka blank) day
function createUnusedDay(number) {
  return {
    number: number,
    text: "",
    subtext: "",
    status: "unused",
    today: false,
    first: false,
    rawDate: "",
    index: -1,
  };
}

// Converts the days array into an array of weeks, where each week is an
// array of 7 days
//
// Input: array of day objects
//
// Output: array of week objects
//  week: array of 7 day objects
function convertDaysToWeeks(days) {
  let weeks = [];

  for (let i = 0, len = days.length; i < len; i += 7) {
    weeks.push(days.slice(i, i + 7));
  }

  return weeks;
}

// Returns an array containing the names of the days as 3-character strings
export function getShortDayNames() {
  return ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
}
