/** @format */

import React from "react";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Header from "./Header";
import Week from "./Week";
import { getWeeks, getShortDayNames } from "./ComposeWeeks";
import SummaryCard from "./SummaryCard";

const useStyles = makeStyles((/*theme*/) => ({
  g7root: {
    flexGrow: 1,
    padding: "10px",
  },
  summary: {
    paddingBottom: "16px",
  },
}));

const Grid7 = (props) => {
  const classes = useStyles();
  const weeks = getWeeks(props.data);
  const { summary, dayOnClick, holidays } = props;

  let weekNum = 0;

  return (
    <>
      <Grid container className={classes.g7root} spacing={0}>
        <Grid item xs={12} className={classes.summary}>
          <SummaryCard info={summary} />
        </Grid>
        <Header values={getShortDayNames()} />
        {weeks.map((week) => (
          <Week
            values={week}
            key={weekNum++}
            dayOnClick={dayOnClick}
            holidays={holidays}
          />
        ))}
      </Grid>
    </>
  );
};

export default Grid7;
