/** @format */

// Implements the individual cells in the header row

import React from "react";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  dayRoot: {
    textAlign: "center",
    color: theme.palette.primary.headerText,
  },
}));

// expects a property named text containing a string to place in the cell
const HeaderDay = (props) => {
  const classes = useStyles();
  const { text } = props;

  return (
    <>
      <Typography className={classes.dayRoot}>{text}</Typography>
    </>
  );
};

export default HeaderDay;
